import  { Router } from 'express';
import { Factory } from '../Factory';

let router = Router();


let factory = new Factory();
let auth = factory.getAuth();

router.get('/v1/auth', (req, res, next ) => {
    if(auth.login()){
        res.json({
            msg: 'success',
        })
        return;
    }

    res.json({
        msg: 'false '+ auth.login(),
    })
});

module.exports = router;
